






































































import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import ProForm from "@/components/pro-form/pro-form.vue";
import myDialog from "@/resources/components/my-dialog/my-dialog.vue";
import debounce from "lodash.debounce";
@Component({
    components: {
        ProForm,
        myDialog
    }
})
export default class ProFormDialog extends Mixins(TwoWay) {
    /** 是否可见 */
    @Prop({ default: false }) visible!: boolean;
    /** 组件模式 */
    @Prop({ default: "edit" }) mode!: string;

    /** 是否可见 */
    @Prop({ default: true }) showClose!: boolean;

    /** 标签对齐方式 */
    @Prop({ default: "right" }) labelPosition!: string;

    /** 模态框标题 */
    @Prop() title?: string;

    /** 模态框宽度 */

    @Prop() width?: string;

    /** 表单列表 */
    @Prop({ default: () => [] }) formList!: any[];

    /** 提交操作 */
    @Prop({ default: () => ({}) }) submitHandler!: () => Promise<any>;

    /** 是否显示提交按钮 */
    @Prop({ type: Boolean, default: true }) showSubmit!: boolean;

    /** 是否显示重置按钮 */
    @Prop({ type: Boolean, default: false }) showReset!: boolean;

    /** 是否显示取消按钮 */
    @Prop({ type: Boolean, default: true }) showCancel!: boolean;

    /** 提交按钮文字 */
    @Prop({ default: "提交" }) buttonSubmitText!: string;

    /** 表单标签宽度 */
    @Prop() labelWidth?: string;

    /** 表单空间大小（element控件） */
    @Prop() size?: string;

    /** 流式布局（不固定高度） */
    @Prop({ type: Boolean, default: false }) fluid!: boolean;

    /** 当弹窗开启或关闭不保留值 */
    @Prop({ type: Boolean, default: false }) resetWithVisible!: boolean;

    /** 表单顶部偏移 */
    @Prop({ default: "10vh" }) top!: string;

    /** 内容区域加载中覆盖层 */
    @Prop({ default: false, type: Boolean }) loading!: boolean;

    btnLoading = false;

    /** 可见状态的计算属性 */
    get currentVisible() {
        return this.visible;
    }

    set currentVisible(val) {
        this.$emit("update:visible", val);
    }

    /** 需要插槽的列表 */
    get slotedItems() {
        const result: any[] = [];
        for (const group of this.formList) {
            for (const item of group.items) {
                if (item.type === "slot") {
                    result.push(item.prop);
                }
            }
        }
        return result;
    }

    @Watch("visible")
    onResetWithVisibleChanghe(newVal: any) {
        if (this.resetWithVisible) {
            window.setTimeout(() => {
                (this.$refs["pro-form"] as ProForm).clearValidate();
            }, 123);
        }
    }

    /** 发送刷新事件 */
    emitSubmit = debounce(
        function(this: ProFormDialog) {
            this.btnLoading = true;
            const result = this.submitHandler();
            if (result instanceof Promise) {
                result.finally(() => {
                    this.btnLoading = false;
                });
            } else {
                this.btnLoading = false;
            }
        },
        650,
        { leading: true }
    );
    // 取消
    handelCancelBtnClick() {
        this.$emit("update:visible", false);
        this.$emit("cancel-click");
    }
    // 点击关闭
    handleDialogClose() {
        this.clearValidate();
        this.$emit("on-dialog-close");
    }
    // 提交
    handleSubmitBtnClick() {
        this.$emit("submit-click");
        if (typeof this.submitHandler === "function") {
            this.validate((valid: boolean) => {
                if (valid) {
                    this.emitSubmit();
                }
            });
        }
    }
    /** 验证表单 */
    validate(callback: Function) {
        (this.$refs["pro-form"] as ProForm).validate(callback);
    }
    /** 重置表单 */
    resetFields() {
        (this.$refs["pro-form"] as ProForm).resetFields();
    }

    /** 重置表单校验 */
    clearValidate() {
        (this.$refs["pro-form"] as ProForm).clearValidate();
    }
    // 清空
    handleResetBtnClick() {
        this.resetFields();
    }
}
