





































































import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import ProFormItem from "./pro-form-item.vue";
import { split2Arr } from "@/utils/shared/tools";
import { validate } from "@/utils/helper/form-validator";

import { Form } from "element-ui";
@Component({ components: { ProFormItem } })
export default class ProForm extends Mixins(TwoWay) {
    @Prop({ type: String, default: "edit" }) mode!: string;
    @Prop({ type: Boolean, default: true }) showOperation!: boolean;
    @Prop({ type: Boolean, default: true }) showReset!: boolean;
    @Prop({ type: Boolean, default: true }) showSave!: boolean;
    @Prop({ type: Boolean, default: true }) showCancel!: boolean;

    @Prop() list!: any[];

    @Prop({ default: "120px" }) labelWidth!: string;

    formRules = {};
    @Watch("list")
    watchList() {
        this.calcformRules();
    }
    // 模式改变 清空校验
    @Watch("mode")
    watchMode() {
        this.clearValidate();
    }
    // 切割二维数组方法
    split2Arr = split2Arr;
    // 综合校验规则
    calcformRules() {
        const result: any = {};
        const { list } = this;
        let allItems: any = [];

        for (const group of list) {
            allItems = [].concat(allItems).concat(group.items);
        }
        for (const item of allItems) {
            if (Array.isArray(item.rules) && item.rules.length) {
                result[item.prop] = item.rules;
            }
        }
        this.formRules = result;
    }
    validate(callback: Function) {
        validate(this, "pro-form").then(valid => {
            callback(valid);
        });
    }

    validateField(prop: string, cb?: any) {
        (this.$refs["pro-form"] as ProForm).validateField(prop, cb);
    }

    resetFields() {
        (this.$refs["pro-form"] as ProForm).resetFields();
    }

    clearValidate() {
        (this.$refs["pro-form"] as ProForm).clearValidate();
    }
    saveHandle() {
        this.validate((valid: boolean) => {
            if (valid) {
                this.$emit("saveHandle");
            }
        });
    }
    cancelHandle() {
        this.$emit("cancelHandle");
    }
    resetHandle() {
        this.clearValidate();
        this.resetFields();
    }
    created() {
        this.calcformRules();
    }
}
