
/**
 * @params fn  需要缓存的函数
 * @description 创建一个可提供缓存的缓存函数
 */
export function cached(fn: Function): Function {
    const cache = Object.create(null)
    return (function cachedFn(str: string) {
        const hit = cache[str]
        return hit || (cache[str] = fn(str))
    })
}

/**
 * @description 将指定数组切割为二维数组
 * @num 按多少分为一组
 */

export function split2Arr(arr: any[], num: number) {
    let newArr = [];
    for (let i = 0; i < arr.length;) {
        newArr.push(arr.slice(i, (i += num)));
    }
    return newArr;
}

/**
 * @param {Object}  obj
 * @description 判断是否有children属性 且为数组 长度大于0
 */
export function hasChildren(item: any) {
    return item.children && item.children.length
}
