import Vue from "vue";
import { Form } from "element-ui";

type triggerType = "blur" | "change";

export class ValidatorRequired {
    required = true;
    message!: string;
    trigger!: triggerType;

    constructor(message = "", trigger: triggerType = "blur") {
        this.message = message;
        this.trigger = trigger;
    }
}
export class ValidatorRegExp {
    pattern!: RegExp;
    message!: string;
    trigger!: triggerType;

    constructor(
        pattern: RegExp,
        message = "格式不正确",
        trigger: triggerType = "blur"
    ) {
        this.pattern = pattern;
        this.message = message;
        this.trigger = trigger;
    }
}

export class ValidatorIsBlank {
    required = true;
    pattern = /^((?!\s).)*$/;
    message!: string;
    trigger!: triggerType;

    constructor(message = "", trigger: triggerType = "blur") {
        this.message = message ? `${message}且不能包含空格` : "不能包含空格";
        this.trigger = trigger;
    }
}

export class ValidatorMinMax {
    min?: number;
    max?: number;
    message!: string;
    trigger!: triggerType;

    constructor(
        min?: number,
        max?: number,
        message?: string,
        trigger: triggerType = "blur"
    ) {
        let finalMessage = "";
        if (!message) {
            if (min && max) {
                finalMessage = `长度应在${min}到${max}个字之间`;
            } else if (min && !max) {
                finalMessage = `长度应大于${min}个字`;
            } else if (!min && max) {
                finalMessage = `长度应小于${max}个字`;
            }
        } else {
            finalMessage = message;
        }

        this.min = min;
        this.max = max;
        this.message = finalMessage;
        this.trigger = trigger;
    }
}
/**
 * 表单校验
 * @param $vue 当前vue实例
 * @param formName form ref名
 */
export const validate = ($vue: Vue, formName: string) => {
    return new Promise(resolve => {
        ($vue.$refs[formName] as Form).validate(
            (valid: boolean, object: any) => {
                if (valid) {
                    resolve(true);
                } else {
                    $vue.$nextTick(() => {
                        const isError = document.getElementsByClassName(
                            "is-error"
                        )[0];
                        const animationError = isError.getElementsByClassName(
                            "el-form-item__error"
                        )[0];
                        animationError.classList.add("jello");

                        isError.scrollIntoView({
                            block: "center",
                            behavior: "smooth"
                        });

                        animationError.addEventListener("animationend", () => {
                            animationError.classList.remove("jello");
                        });
                    });
                    resolve(false);
                }
            }
        );
    });
};
